a,
.control {
  color: #34345c;
}
.bfloat > svg {
  fill: #34345c;
}
.bmodal hr {
  border-top: 1px solid #34345c;
}
#banner {
  background: rgba(214, 218, 240, 0.7);
  border-bottom: 1px solid #b7c5d9;
}
b {
  color: #117743;
}
b.admin {
  color: #f00000;
}
b.moderator {
  color: purple;
}
a:hover,
blockquote a {
  color: #d00;
}
article,
aside,
.pagination {
  background-color: #d6daf0;
  border-right: 1px solid #b7c5d9;
  border-bottom: 1px solid #b7c5d9;
}
article.highlight {
  background-color: #d6bad0;
  border-color: #ba9dbf;
}
article.editing {
  background-color: #fa6;
  border-color: #e95;
}
body {
  background-color: #eef2ff;
}
body,
pre,
.themed {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 10pt;
}
em {
  color: #789922;
}
h1 {
  color: #af0a0f;
  font: bolder 28px Tahoma;
  letter-spacing: -2px;
}
h2 {
  color: #af0a0f;
  font-family: Tahoma;
}
h3 {
  color: #0f0c5d;
}
header nav a {
  color: black;
}
hr {
  border-top: 1px solid #b7c5d9;
}
.popup-menu li:hover {
  background-color: #f5f5f5;
}
.email {
  color: #0f0c5d;
}
.modal,
.bmodal,
.preview,
.popup-menu {
  background-color: white;
  border-right: 1px solid #eee;
  border-bottom: 1px solid #eee;
}
.option_tab_sel a.tab_sel {
  color: #117743;
}
.omit {
  color: gray;
}
